<template>
  <div>
    <header
      style="
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        background: rgba(255, 255, 255, 0.8);
        z-index: 1000;
      "
    >
      <div style="background: gray; color: white">
        <ul v-show="showMenu">
          <li><input type="checkbox" v-model="showEdit" />显示管理按钮</li>
          <li @click="load">Reload</li>
        </ul>
        <div>
          <font-awesome-icon
            :icon="showMenu ? 'angle-up' : 'angle-down'"
            fixed-width
            @click="showMenu = !showMenu"
          />
        </div>
      </div>

      <h4 style="margin: 5px">宝贝计划</h4>

      <div class="tabs" style="clear: both">
        <ul>
          <li
            class="tab"
            v-for="(tab, i) in tabs"
            @click="curTabIndex = i"
            :key="i"
            :class="{ curTab: curTabIndex == i }"
          >
            {{ tab.name }}
          </li>
        </ul>
      </div>
    </header>
    <div class="swiper-box" style="margin-top: 110px">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item, index) in tabs" :key="index">
            <keep-alive>
              <component :is="item.comp"></component>
            </keep-alive>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script >
import ByVideo from "./ByVideo";
import ByImage from "./ByImage";
import ByAudio from "./ByAudio";
import ByWhen from "./ByWhen";
import EditWords from "./EditWords";
import Swiper from "swiper";
import "swiper/swiper-bundle.css";
var mySwiper;
export default {
  components: {
    ByVideo,
    ByAudio,
    ByImage,
    ByWhen,
    EditWords,
  },
  data() {
    return {
      curTabIndex: 0,
      showMenu: 0,
      tabs: [
        { name: "视频学习", comp: "ByVideo" },
        { name: "看图识字", comp: "ByImage" },
        { name: "音乐", comp: "ByAudio" },
        { name: "考考你", comp: "ByWhen" },
        { name: "EditWords", comp: "EditWords" },
      ],
    };
  },
  mounted() {
    mySwiper = new Swiper(".swiper-container", {
      initialSlide: 0,
    });
    mySwiper.on("slideChange", () => {
      // 监控滑动后当前页面的索引，将索引发射到导航组件
      // 左右滑动时将当前slide的索引发送到nav组件
      this.curTabIndex = mySwiper.activeIndex;
      //this.$root.eventHub.$emit("slideTab", mySwiper.activeIndex);
    });
    // 接收nav组件传过来的导航按钮索引值，跳转到相应内容区
    /*this.$root.eventHub.$on("changeTab", (index) => {
      // 点击导航键跳转相应内容区
      mySwiper.slideTo(index, 0, false);
    });
    console.log(mySwiper);*/
  },

  computed: {
    showEdit: {
      get() {
        return this.$store.state.showEdit;
      },
      set(value) {
        this.$store.commit("showEdit", value);
      },
    },
  },
  methods: {
    load() {
      fetch("/api/reLoadPlayList", {
        method: "get",
      }).then((data) => {});
    },
  },
  watch: {
    curTabIndex() {
      if (
        ["ByWhen", "EditWords"].indexOf(this.tabs[this.curTabIndex].comp) > -1
      ) {
        this.$store.commit("showPlayer", false);
      } else this.$store.commit("showPlayer", true);
      mySwiper.slideTo(this.curTabIndex, 0, false);
    },
  },
};
</script>

<style scoped>
.tab {
  cursor: pointer;
}
.curTab {
  background: white;
}

.curTab:after {
  content: "";
  width: auto;
  min-width: 44px;
  height: 2px;
  background: #4e6ef2;
  border-radius: 1px;
  display: block;
  margin-top: 1px;
}
ul {
  list-style-type: none;
  padding: 0;
}
.tabs li {
  display: inline-block;
  margin: 0 10px;
}
</style>
